$font-path: '/catalog/view/theme/default/fonts';


@font-face {
  font-family: 'Futura';
  src: url('#{$font-path}/');
  src: url('#{$fa-font-path}/furuta.eot') format('embedded-opentype'),
    // url('#{$font-path}/furuta.woff2') format('woff2'),
    url('#{$font-path}/furuta.woff') format('woff'),
    url('#{$font-path}/furuta.ttf') format('truetype'),
    url('#{$font-path}/furuta.svg') format('svg');
  font-weight: normal;
  font-style: normal;
} 